







































































import { Component, Vue } from "vue-property-decorator";
import XLSX from 'xlsx';
import moment from "moment";
import axios from "axios";

@Component
export default class QueryUser extends Vue {
  projects: Array<any>=[];
  date: any = null;
  dataProcessed: Array<any> = [];
  projectSelected: any = null;
  total = 0;
  isLoading = false;
  startDate: any = null;
  endDate: any = null;

  created(){
    this.getUniquesProjects();
  }


  async getUniquesProjects(){
    const response = await axios.post('/api/project-list');
    this.projects = response.data;
  }

  projectChange(index: any){
    this.projectSelected =  (typeof index === 'number') ? this.projects[index].project : null;
    this.getData();
  }

  dateRange(date: any){
    if (date.length <= 0) return true;
    this.startDate  = moment(date[0]._d).format('YYYY-MM-DD'); //this.getInitialDay();
    this.endDate = moment(date[1]._d).format('YYYY-MM-DD');
    this.getData();
  }

  async getData(){
    if (this.startDate == null && this.endDate == null) return true;
    this.isLoading = true;
    const result = await axios.post('/api/projects-registers-user-list',{
      start: this.startDate,
      end: this.endDate,
      project: this.projectSelected
    });
    this.isLoading = false;
    this.total = result.data.total;
    this.dataProcessed = result.data.rows;
  }

  goToQueryChart(){
    this['$router'].replace("/query-user-chart");
  }

  dataExport(){
    const headers = ['project', 'detail', 'dateStart', 'dateEnd', 'hours'];
    const data = XLSX.utils.json_to_sheet( this.dataProcessed, {
      header: headers
    })
    data['A1'].v = 'Proyecto'
    data['B1'].v = 'Detalle'
    data['C1'].v = 'Inicio'
    data['D1'].v = 'Fin'
    data['E1'].v = 'Horas'

    const workbook = XLSX.utils.book_new()
    const filename = 'events_exports'
    XLSX.utils.book_append_sheet(workbook, data, filename)
    XLSX.writeFile(workbook, `${filename}.xlsx`)
  }
}
